<template xmlns="http://www.w3.org/1999/html">
  <div class="tile is-ancestor">
    <div class="tile is-parent">
      <article class="tile is-child box">
        <h4 class="title"> </h4>
        <div class="block">
          <label class="label"> </label>
          <div class="select is-fullwidth">
            <select v-model="data.rq_process_status" v-on:change="changeProcessStatus">
              <option v-for="status in requestStatusConfig.statusList">{{status.label}}</option>
            </select>
          </div>
          <label class="label" v-if="hasStatusChildren"> </label>
          <div class="select is-fullwidth" v-if="hasStatusChildren">
            <select v-model="data.rq_fail_reason">
              <option value="" selected="selected"></option>
              <option v-for="failStatus in failStatusList" :value="failStatus.label">{{failStatus.label}}</option>
            </select>
          </div>
          <label class="label"></label>
          <p class="control">
            {{ (data.rq_reg_dt === '0000-00-00' || !data.rq_reg_dt) ? '' : moment(data.rq_reg_dt, 'YYYY-MM-DDTHH:mm:ss').format('YYYY-MM-DD') }}
          </p>
          <label class="label"></label>
          <p class="control">
            {{ data.rq_name }}
          </p>
          <label class="label"></label>
          <p class="control">
            <cleave class="input" type="tel" placeholder="Enter phone number" :options="{ phone: true, phoneRegionCode: 'kr'}" v-model="data.rq_phone" v-on:change="updateValue">

            </cleave>
          </p>
          <label class="label"></label>
          <p class="control">
            <input class="input" type="text" v-model="data.rq_manager" />
          </p>
          <label class="label"></label>
          <p class="control">
            <input type="text" class="input" v-model="data.rq_nickname" />
          </p>
          <label class="label"> </label>
          <div class="control">
            <div class="select is-fullwidth">
              <select v-model="data.rq_site_type">
                <option value=""></option>
                <option v-for="siteType in requestStatusConfig.siteTypeList" :value="siteType.label">{{siteType.label}}</option>
              </select>
            </div>
          </div>
          <label class="label"></label>
          <div class="control">
            <div class="select is-fullwidth">
              <select v-model="data.rq_size">
                <option value=""> </option>
                <option value="lt20">20 </option>
                <option value="eq20">20</option>
                <option value="eq30">30</option>
                <option value="eq40">40</option>
                <option value="eq50">50</option>
                <option value="eq60">60</option>
                <option value="gte70">70 </option>
              </select>
            </div>
          </div>
          <label class="label"></label>
          <div class="control">
            <div class="select is-fullwidth">
              <select v-model="data.rq_budget">
                <option value=''> </option>
                <option value='1500~2000'>1500~2000</option>
                <option value='2000~2500'>2000~2500</option>
                <option value='2500~3000'>2500~3000</option>
                <option value='3000~3500'>3000~3500</option>
                <option value='3500~4000'>3500~4000</option>
                <option value='4000~4500'>4000~4500</option>
                <option value='4500~5000'>4500~5000</option>
                <option value='5000~5500'>5000~5500</option>
                <option value='5500~6000'>5500~6000</option>
                <option value='6000~6500'>6000~6500</option>
                <option value='6500~7000'>6500~7000</option>
                <option value='lt1500'>1500 </option>
                <option value='lt2000'>2000 </option>
                <option value='lt2500'>2500 </option>
                <option value='lt3000'>3000 </option>
                <option value='lt3500'>3500 </option>
                <option value='lt4000'>4000 </option>
                <option value='lt4500'>4500 </option>
                <option value='lt5000'>5000 </option>
                <option value='gte2500'>2500 </option>
                <option value='gte3000'>3000 </option>
                <option value='gte3500'>3500 </option>
                <option value='gte4000'>4000 </option>
                <option value='gte4500'>4500 </option>
                <option value='gte5000'>5000 </option>
                <option value='gte6000'>6000 </option>
                <option value='gte7000'>7000 </option>
                <option value='contact'> </option>
              </select>
            </div>
          </div>
          <label class="label"></label>
          <p class="control">
            <label></label>
            <input class="input" type="text" v-model="data.rq_address_brief" />
            <label></label>
            <input class="input" type="text" v-model="data.rq_address_detail" />
          </p>
          <label class="label"> </label>
          <p class="control">
            <datepicker v-model="data.rq_move_date" />
          </p>
          <label class="label"> </label>
          <p class="control">
            <datepicker v-model="data.rq_date" />
          </p>
          <label class="label"> </label>
          <div class="control">
            <div class="select is-fullwidth">
              <select v-model="data.rq_time">
                <option value=""></option>
                <option value="09:00 - 11:00">09:00 - 11:00</option>
                <option value="11:00 - 13:00">11:00 - 13:00</option>
                <option value="13:00 - 15:00">13:00 - 15:00</option>
                <option value="15:00 - 17:00">15:00 - 17:00</option>
                <option value="17:00 - 19:00">17:00 - 19:00</option>
              </select>
            </div>
          </div>
          <label class="label"></label>
          <div class="control">
            <div class="is-fullwidth">
              <textarea class="textarea" name="rq_construction_type" v-model="data.rq_construction_type"></textarea>
            </div>
          </div>
          <label class="label"></label>
          <div class="control">
            <div class="is-fullwidth">
              <textarea class="textarea" name="rq_consulting_result" v-model="data.rq_consulting_result"></textarea>
            </div>
          </div>
          <label class="label"></label>
          <div class="control">
            <div class="is-fullwidth">
              <textarea class="textarea" name="rq_memo" v-model="data.rq_memo"></textarea>
            </div>
          </div>
          <p class="control">
            <button class="button is-primary" v-on:click="submitData">Submit</button>
            <button class="button is-link">Cancel</button>
          </p>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
  import Datepicker from 'vue-bulma-datepicker'
  import moment from 'moment'
  import Cleave from 'vue-cleave'
  import 'cleave.js/dist/addons/cleave-phone.kr.js'
  import requestStatusConfig from '../../config/request-status-config'
  import _ from 'underscore'
  import Vue from 'vue'
  import Notification from 'vue-bulma-notification'

  const NotificationComponent = Vue.extend(Notification)
  // @TODO require Cleave library bug fixed
  const openNotification = (propsData = {
    title: '',
    message: '',
    type: '',
    direction: '',
    duration: 4500,
    container: '.notifications'
  }) => {
    return new NotificationComponent({
      el: document.createElement('div'),
      propsData
    })
  }

  const queryApi = '/api/request'

  export default {
    name: 'detail',
    components: {
      Datepicker,
      Cleave
    },
    data () {
      return {
        data: {
          rq_phone: '',
          rq_fail_reason: '',
          rq_site_type: ''
        },
        id: '',
        hasStatusChildren: false,
        failStatusList: [],
        requestStatusConfig,
        moment
      }
    },
    mounted () {
      console.log(this.$route)
      this.id = this.$route.params.id
      if (!this.id) {
        this.$router.back()
      }
      this.loadDetail(this.id)
    },
    methods: {
      loadDetail (id) {
        console.log(`${queryApi}/${id}`)
        this.$http.get(`${queryApi}/${id}`)
        .then((response) => {
          console.log(response)
          if (response.data.code !== 200) {
            return
          }
          this.data = response.data.data.data
          if (this.data.rq_fail_reason) {
            this.failStatusList = _.find(this.requestStatusConfig.statusList, (item) => {
              return item.label === this.data.rq_process_status
            }).children
            this.hasStatusChildren = true
          }
          Vue.set(this.data, 'rq_phone', this.data.rq_phone)
        }).catch((error) => {
          console.log(error)
        })
      },
      updateValue (newValue, oldValue) {
        console.log(`newValue: ${newValue}`)
        console.log(`oldValue: ${oldValue}`)
      },
      validate () {
        if (this.data.rq_name === '') {
          openNotification({
            message: '  .',
            type: 'danger',
            duration: 2500
          })
          return false
        } else if (this.data.rq_phone === '') {
          openNotification({
            message: '  .',
            type: 'danger',
            duration: 2500
          })
          return false
        }

        return true
      },
      submitData () {
        if (this.validate()) {
          this.data.rq_phone = this.data.rq_phone.replace(/\s/gi, '')
          this.$http.put(`${queryApi}/${this.id}`, this.data)
            .then((response) => {
              console.log(response)
              if (response.data.code !== 200) {
                return false
              }
              openNotification({
                message: '  .',
                type: 'success',
                duration: 2500
              })
              this.$router.back()
            }).catch((error) => {
              console.log(error)
            })
        }
      },
      changeProcessStatus () {
        const selectedData = _.find(this.requestStatusConfig.statusList, (item) => {
          return item.label === this.data.rq_process_status
        })
        this.data.rq_fail_reason = ''
        if (selectedData.hasOwnProperty('children')) {
          this.failStatusList = selectedData.children
          this.hasStatusChildren = true
        } else {
          this.failStatusList = []
          this.hasStatusChildren = false
        }
        this.$forceUpdate()
      }
    }
  }
</script>

<style scoped>

</style>



