<template>
  <div class="estimate-detail">
    <div class="tabs is-boxed">
      <ul>
        <li @click="activeView(tabType.info)" :class="{'is-active': currentTab === tabType.info}"><a></a></li>
        <li @click="activeView(tabType.preEstimateView)" :class="{'is-active': currentTab === tabType.preEstimateView}"><a></a></li>
        <li @click="activeView(tabType.estimateView)" :class="{'is-active': currentTab === tabType.estimateView}"><a></a></li>
        <li @click="activeView(tabType.managerAndShop)" :class="{'is-active': currentTab === tabType.managerAndShop}"><a>  </a></li>
        <li @click="activeView(tabType.siteImage)" :class="{'is-active': currentTab === tabType.siteImage}"><a></a></li>
        <li @click="activeView(tabType.checkList)" :class="{'is-active': currentTab === tabType.checkList}"><a></a></li>
      </ul>
    </div>
    <div class="tile is-ancestor">
      <div class="tile is-parent">
        <article class="tile is-child box" v-show="currentTab === tabType.info">
          <div class="block">
            <label class="label"></label>
            <div class="control">
              <input class="input" type="text" v-model="detailData.pc_name" :class="{'is-danger': $v.detailData.pc_name.$invalid }" />
              <p class="help is-danger" v-if="!$v.detailData.pc_name.required">  .</p>
            </div>
            <label class="label"></label>
            <div class="control">
              <input class="input" type="text" v-model="detailData.pc_phone" :class="{'is-danger': $v.detailData.pc_phone.$invalid }" />
              <p class="help is-danger" v-if="!$v.detailData.pc_phone.required">  .</p>
            </div>
            <label class="label"></label>
            <p class="control" v-text="detailData.pc_password">
            </p>
            <label class="label"></label>
            <p class="control">
              <input class="input" type="text" v-model="detailData.pc_size" />
            </p>
            <label class="label"></label>
            <p class="control">
              <input class="input" type="text" v-model="detailData.pc_address_brief" />
            </p>
            <label class="label"> </label>
            <p class="control">
              <input class="input" type="text" v-model="detailData.pc_address_detail" />
            </p>
            <label class="label"></label>
            <p class="control">
              <datepicker placeholder=" " :config="{ dateFormat: 'Y-m-d', static: true }" v-model="detailData.pc_move_date"></datepicker>
            </p>
            <label class="label"></label>
            <p class="control">
              <input class="input" type="text" v-model="detailData.pc_budget"/>
            </p>
            <label class="label"></label>
            <p class="control">
              <textarea class="textarea" v-model="detailData.pc_memo"></textarea>
            </p>
            <label class="label">(%)</label>
            <p class="control">
              <input class="input" v-model="detailData.pc_etc_costs_ratio" />
            </p>
            <label class="label">  (%)</label>
            <p class="control">
              <input class="input" v-model="detailData.pc_design_costs_ratio" />
            </p>
            <label class="label">(%)</label>
            <p class="control">
              <input class="input" v-model="detailData.pc_supervision_costs_ratio" />
            </p>
            <label class="label"></label>
            <p class="control">
              <input class="input" v-model="detailData.pc_discount_amount" />
            </p>
            <p class="control">
              <button class="button is-primary" @click="updateContract"></button>
              <button class="button is-link" @click="router.back()">Cancel</button>
              <button class="button is-danger is-pulled-right" @click="deleteContract"></button>
            </p>
          </div>
        </article>
        <article class="tile is-child box" v-show = "currentTab === tabType.preEstimateView">
          <estimate-sheet :estimateIsPre="true"/>
        </article>
        <article class="tile is-child box estimate" v-show = "currentTab === tabType.estimateView">
          <estimate-sheet :estimateIsPre="false"/>
        </article>
        <article class="tile is-child box" v-show="currentTab === tabType.managerAndShop">
          <p class="subtitle is-3 is-pulled-left"></p>
          <a class="button is-primary is-pulled-right is-medium" id="addConstructorBtn" @click="openAddPartnerModal('constructor')"></a>
          <table class="table is-bordered">
            <colgroup>
              <col width="7%"/>
              <col width="8%"/>
              <col width="15%"/>
              <col width="10%"/>
              <col width="10%"/>
              <col width="30%"/>
              <col width="10%"/>
            </colgroup>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
            <tr v-for="item in partners.constructor">
              <td>{{item.ct_name}}</td>
              <td>{{item.cr_name}}</td>
              <td>{{item.cr_contact}}</td>
              <td>
                <star-rating v-model="item.cr_communication_score" :show-rating="false" :star-size="15" :read-only="true" />
              </td>
              <td>
                <star-rating v-model="item.cs_skill_score" :show-rating="false" :star-size="15" :read-only="true" />
              </td>
              <td>{{item.cs_memo}}</td>
              <td>
                <button class="button is-danger" @click="deletePartners(item, 'constructor')"></button>
              </td>
            </tr>
            </tbody>
          </table>
          <p class="subtitle is-3 is-pulled-left"></p>
          <a class="button is-primary is-pulled-right is-medium" id="addCorrespondentBtn" @click="openAddPartnerModal('correspondent')"></a>
          <table class="table is-bordered">
            <colgroup>
              <col width="10%"/>
              <col width="20%"/>
              <col width="15%"/>
              <col width="15%"/>
              <col width="30%"/>
              <col width="10%"/>
            </colgroup>
            <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in partners.correspondent">
              <td>{{item.rc_name}}</td>
              <td>{{item.co_name}}</td>
              <td>{{item.co_contact}}</td>
              <td>{{item.co_manager_name}}</td>
              <td>{{item.ci_brand}}</td>
              <td>
                <button class="button is-danger" @click="deletePartners(item, 'correspondent')"></button>
              </td>
            </tr>
            </tbody>
          </table>
        </article>
        <article class="tile is-child box" v-show="currentTab === tabType.siteImage">
          <p class="subtitle is-3 is-pulled-left"></p>
          <a class="button is-primary is-pulled-right is-medium" id="addSiteImageBtn" @click="openAddSiteImageModal()"></a>
          <table class="table">
            <colgroup>
              <col width="50%;"/>
              <col width="auto;"/>
              <col width="10%;"/>
              <col width="10%;" />
            </colgroup>
            <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item) in siteImageList">
              <td>
                <a :href="item.si_url">
                  <img :src="item.si_url" />
                </a>
              </td>
              <td>{{item.si_description}}</td>
              <td>{{getComputedDate(item.si_reg_dt)}}</td>
              <td><button class="button" v-on:click.stop="deleteImageRow(item)"></button></td>
            </tr>
            <tr v-if="siteImageList.length === 0">
              <td colspan="5" class="has-text-centered">  .</td>
            </tr>
            </tbody>
          </table>
        </article>
        <article class="tile is-child box" v-show="currentTab === tabType.checkList">
          <p class="subtitle is-3 is-pulled-left"></p>
          <a class="button is-primary is-pulled-right is-medium" @click="moveToDate"></a>
          <span class="is-pulled-right" style="height:2.885rem">
            <datepicker ref="moveToDate" style="width:100px; vertical-align: middle" v-model="wantMoveDate"/> <b style="vertical-align: middle;"></b>
          </span>
          <div>
            <table class="table is-bordered check-list">
            <colgroup>
              <col width="5%"/>
              <col width="16%"/>
              <col width="16%"/>
              <col width="5%"/>
              <col width="5%"/>
              <col width="16%"/>
            </colgroup>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody v-for="(checkListByDate, index) in checkList">
              <tr @click="toggleCheckDate(index)" class="date-header">
                <th colspan="6">{{index}}</th>
              </tr>
              <tr v-for="checkListItem in checkListByDate">
                <td class="has-text-centered">
                  <input type="checkbox" class="checkbox" v-model="checkListItem.isChecked">
                </td>
                <td>
                  <span v-if="!checkListItem.isModify">{{checkListItem.date}}</span>
                  <datepicker v-if="checkListItem.isModify" v-model="checkListItem.date" />
                </td>
                <td>
                  <span v-if="!checkListItem.isModify">{{checkListItem.ct_name}}</span>
                  <div class="select" v-if="checkListItem.isModify">
                    <select v-model="checkListItem.cl_ctpk">
                      <option value=""></option>
                      <option v-for="construction in currentConstructionList" :value="construction.ct_pk">
                        {{construction.ct_name}}
                      </option>
                    </select>
                  </div>
                </td>
                <td>
                  <input type="checkbox" class="checkbox" v-model="checkListItem.cl_resource" @change="updateCheckListStatus(checkListItem)" />
                </td>
                <td>
                  <input type="checkbox" class="checkbox" v-model="checkListItem.cl_constructor" @change="updateCheckListStatus(checkListItem)" />
                </td>
                <td>
                  <button class="button" v-if="!checkListItem.isModify" @click="checkListItem.isModify = true"></button>
                  <button class="button is-danger" v-if="!checkListItem.isModify" @click="deleteCheckList(checkListItem)"></button>
                  <button class="button" v-if="checkListItem.isModify" @click="checkListItem.isModify = false"></button>
                  <button class="button is-info" v-if="checkListItem.isModify" @click="updateCheckListStatus(checkListItem)"></button>
                </td>
              </tr>
            </tbody>
            <tbody>
              <tr v-show="!isAddCheckList">
                <td colspan="6" class="has-text-centered" @click="isAddCheckList = true;">+</td>
              </tr>
              <tr v-show="isAddCheckList">
                <td>
                  <!--<input type="checkbox" class="checkbox">-->
                </td>
                <td>
                  <datepicker v-model="newCheckList.date"/>
                </td>
                <td>
                  <div class="select">
                    <select v-model="newCheckList.ct_pk">
                      <option value=""></option>
                      <option v-for="construction in currentConstructionList" :value="construction.ct_pk">
                        {{construction.ct_name}}
                      </option>
                    </select>
                  </div>
                </td>
                <td>
                  <input type="checkbox" class="checkbox" v-model="newCheckList.cl_resource"/>
                </td>
                <td>
                  <input type="checkbox" class="checkbox" v-model="newCheckList.cl_constructor"/>
                </td>
                <td>
                  <button class="button" @click="registerCheckList"></button>
                  <button class="button is-danger" @click="isAddCheckList = false;"></button>
                </td>
              </tr>
            </tbody>
          </table>
          </div>
        </article>
      </div>
    </div>
    <add-partners-modal
      :title="addPartnersModalData.title"
      :type="addPartnersModalData.type"
      :id="param.id"
      :constructionList="partners.construction"
      :resourceCategoryList="partners.resourceCategory"
      :beforeClose="loadPartner" />

    <add-site-image-modal
      :id="param.id"
      :beforeClose="loadSiteImage" />
  </div>
</template>

<script>
  import estimateSheet from './estimateSheet'
  import router from '../../router'
  import { required } from 'vuelidate/lib/validators'
  import Notification from 'vue-bulma-notification'
  import Vue from 'vue'
  import addPartnersModal from './addPartnersModal'
  import addSiteImageModal from './addSiteImageModal'
  import StarRating from 'vue-star-rating'
  import _ from 'underscore'
  import Datepicker from 'vue-bulma-datepicker'
  import mixin from '../../services/mixin'
  import EventBus from '../../services/eventBus'
  import moment from 'moment'

  const NotificationComponent = Vue.extend(Notification)

  const openNotification = (propsData = {
    title: '',
    message: '',
    type: '',
    direction: '',
    duration: 4500,
    container: '.notifications'
  }) => {
    return new NotificationComponent({
      el: document.createElement('div'),
      propsData
    })
  }

  const queryApi = '/api/contract'

  export default {
    name: 'estimateDetail',
    mixins: [mixin],
    components: {
      estimateSheet,
      addPartnersModal,
      addSiteImageModal,
      StarRating,
      Datepicker
    },
    data () {
      return {
        router,
        moment,
        tabType: {
          info: 'info',
          estimateView: 'estimateView',
          managerAndShop: 'managerAndShop',
          preEstimateView: 'preEstimateView',
          siteImage: 'siteImage',
          checkList: 'checkList'
        },
        currentTab: '',
        param: {},
        detailData: {},
        estimateData: {
          general: [],
          labor: [],
          resource: [],
          total: {}
        },
        addPartnersModalData: {
          type: '',
          title: ''
        },
        partners: {
          constructor: [],
          correspondent: [],
          construction: [],
          resourceCategory: []
        },
        estimateTabList: [],
        siteImageList: [],
        checkList: [],
        isAddCheckList: false,
        currentConstructionList: [],
        isDatepickerOpen: false,
        newCheckList: {
          ct_pk: ''
        },
        wantMoveDate: ''
      }
    },
    validations: {
      detailData: {
        pc_name: {
          required
        },
        pc_phone: {
          required
        }
      }
    },
    mounted () {
      this.currentTab = this.tabType.info
      this.param = this.$route.params
      this.loadDetail()
    },
    computed: {
      getFullAddress () {
        return `${this.detailData.pc_address_brief} ${this.detailData.pc_address_detail}`
      }
    },
    methods: {
      activeView (type) {
        this.currentTab = type
        switch (type) {
          case this.tabType.info:
            this.loadDetail()
            break
          case this.tabType.preEstimateView:
            EventBus.$emit('loadPreEstimateView')
            break
          case this.tabType.estimateView:
            EventBus.$emit('loadEstimateView')
            break
          case this.tabType.managerAndShop:
            this.loadPartner()
            break
          case this.tabType.siteImage:
            this.loadSiteImage()
            break
          case this.tabType.checkList:
            this.loadCheckList()
            break
        }
      },
      loadDetail () {
        const id = this.param.id
        if (!id) {
          return false
        }
        this.$http.get(`${queryApi}/${id}`)
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }
            this.detailData = response.data.data.contract
          }).catch((error) => {
            console.log(error)
          })
      },
      updateContract () {
        const id = this.param.id

        this.$http.put(`${queryApi}/${id}`, this.detailData)
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }
            openNotification({
              message: '   .',
              type: 'success',
              duration: 1500
            })
          })
          .catch((error) => {
            console.error(error)
          })
      },
      deleteContract () {
        if (window.confirm(' ?')) {
          const id = this.param.id

          this.$http.delete(`${queryApi}/${id}`)
            .then((response) => {
              if (response.data.code !== 200) {
                return false
              }
              openNotification({
                message: '   .',
                type: 'success',
                duration: 1500
              })
              router.back()
            })
            .catch((error) => {
              console.error(error)
            })
        }
      },
      loadPartner () {
        const id = this.param.id

        this.$http.get(`${queryApi}/${id}/constructor`)
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }
            this.partners.constructor = response.data.data.constructorList
            return this.$http.get(`${queryApi}/${id}/correspondent`)
          })
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }
            this.partners.correspondent = response.data.data.correspondentList
            return this.$http.get(`${queryApi}/${id}/construction`)
          })
          .then((response) => {
            this.partners.construction = response.data.data.constructionList
            // return this.$http.get(`${queryApi}/resource/category`)
            return this.$http.get(`${queryApi}/${id}/resource`)
          })
          .then((response) => {
            this.partners.resourceCategory = response.data.data.coresspondentList
          })
          .catch((error) => {
            console.error(error)
          })
      },
      deletePartners (item, type) {
        const id = this.param.id
        const itemPk = item.hasOwnProperty('cc_pk') ? item.cc_pk : item.cco_pk
        this.$http.delete(`${queryApi}/${id}/${type}/${itemPk}`)
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }

            openNotification({
              message: ' .',
              type: 'success',
              duration: 1500
            })
            this.partners[type] = _.without(this.partners[type], item)
          })
      },
      openAddPartnerModal (type) {
        let message = ''
        switch (type) {
          case 'constructor':
            message = ''
            break
          case 'correspondent':
            message = ''
            break
        }
        this.addPartnersModalData.title = `${message} `
        this.addPartnersModalData.type = type

        this.$modal.show('addPartnersModal')
      },
      openAddSiteImageModal () {
        this.$modal.show('addSiteImageModal')
      },
      loadSiteImage () {
        const id = this.param.id
        this.$http.get(`${queryApi}/${id}/image`)
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }
            console.log(response.data.data)
            this.siteImageList = response.data.data.siteImageList
          })
          .catch((e) => {
            console.error(e)
          })
      },
      deleteImageRow (item) {
        const id = this.param.id
        this.$http.delete(`${queryApi}/${id}/image/${item.si_pk}`)
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }
            openNotification({
              message: '.',
              type: 'success',
              duration: 1500
            })
            this.loadSiteImage()
          })
          .catch((e) => {
            console.error(e)
          })
      },
      loadCheckList () {
        const id = this.param.id
        this.$http.get(`${queryApi}/${id}/checklist`)
          .then((response) => {
            if (response.data.code !== 200) {
              return false
            }
            const checkList = response.data.data.checklist
            for (let i = 0; i < checkList.length; i++) {
              checkList[i].date = moment(checkList[i].cl_date, 'X').format('YYYY-MM-DD')
              checkList[i].isModify = false
              checkList[i].isChecked = false
            }
            this.checkList = _.groupBy(checkList, 'date')

            return this.$http.get(`${queryApi}/${id}/construction`)
          })
          .then((response) => {
            if (response.data.code !== 200) {
              return
            }
            this.currentConstructionList = response.data.data.constructionList
          })
          .catch((e) => {
            console.error(e)
          })
      },
      moveToDate () {
        const isCheckedCheckList = _.chain(this.checkList)
          .values()
          .flatten()
          .filter((item) => {
            return item.isChecked === true
          })
          .map((item) => {
            return item.cl_pk
          })
          .value()

        const data = {
          checklist: isCheckedCheckList,
          cl_date: moment(this.wantMoveDate, 'YYYY-MM-DD').format('X')
        }

        const id = this.param.id
        this.$http.put(`${queryApi}/${id}/checklist`, data)
          .then((response) => {
            if (response.data.code !== 200) {
              openNotification({
                message: '     .',
                type: 'danger',
                duration: 1500
              })
              return
            }

            openNotification({
              message: '  .',
              type: 'success',
              duration: 1500
            })
            this.loadCheckList()
          })
          .catch((e) => {
            console.error(e)
          })
      },
      toggleCheckDate (date) {
        const alreadyCheckedList = _.filter(this.checkList[date], (item) => {
          return item.isChecked
        })
        let checkedType = true
        if (alreadyCheckedList.length === this.checkList[date].length) {
          checkedType = false
        }
        _.map(this.checkList[date], (item) => {
          item.isChecked = checkedType
        })
      },
      registerCheckList () {
        this.newCheckList.cl_date = moment(this.newCheckList.date, 'YYYY-MM-DD').format('X')
        console.log(this.newCheckList)
        if (!this.newCheckList.date) {
          window.alert('  .')
          return
        } else if (!this.newCheckList.ct_pk) {
          window.alert('  .')
          return
        }
        const id = this.param.id
        this.$http.post(`${queryApi}/${id}/checklist`, this.newCheckList)
          .then((response) => {
            if (response.data.code !== 200) {
              openNotification({
                message: '    .',
                type: 'danger',
                duration: 1500
              })
              return
            }
            openNotification({
              message: '  .',
              type: 'success',
              duration: 1500
            })
            this.newCheckList.ct_pk = ''
            this.newCheckList.date = ''
            this.newCheckList.cl_constructor = ''
            this.newCheckList.cl_resource = ''
            this.newCheckList.cl_date = ''
            this.isAddCheckList = false
            this.loadCheckList()
          })
          .catch((e) => {
            console.error(e)
          })
      },
      deleteCheckList (item) {
        const id = this.param.id
        this.$http.delete(`${queryApi}/${id}/checklist/${item.cl_pk}`)
          .then((response) => {
            console.log(response)
            if (response.data.code !== 200) {
              openNotification({
                message: '    .',
                type: 'success',
                duration: 1500
              })
              return
            }
            openNotification({
              message: '  .',
              type: 'success',
              duration: 1500
            })
            this.loadCheckList()
          })
      },
      updateCheckListStatus (item) {
        const id = this.param.id
        item.cl_date = moment(item.date, 'YYYY-MM-DD').format('X')
        this.$http.put(`${queryApi}/${id}/checklist/${item.cl_pk}`, item)
          .then((response) => {
            console.log(response)
            if (response.data.code !== 200) {
              openNotification({
                message: '    .',
                type: 'danger',
                duration: 1500
              })
              return
            }
            openNotification({
              message: '  .',
              type: 'success',
              duration: 1500
            })
            this.loadCheckList()
          })
          .catch((e) => {
            console.error(e)
          })
      }
    }
  }
</script>

<style scoped lang="scss">
  .tile.is-parent {
    width: 100%;
    .is-child {
      width: 100%;
    }
  }
  .check-list {
    .date-header {
      cursor: pointer;
    }
  }
</style>

<style scoped lang="scss">
  @media screen and (max-width: 768px) {
    /*.app-content {*/
      /*padding: 20px 0;*/
    /*}*/
    .estimate-detail {
      .tile {
        padding: 0.25rem;
        > div {
          padding: 0.5rem;
        }
      }
    }
  }
</style>



