<template>
  <modal name="addPartnersModal" @before-open="beforeOpen" @before-close="beforeClose" height="auto" width="60%">
    <div class="add-partners-modal">
      <div class="modal-card-head">
        <h1 class="modal-card-title">{{this.title}}</h1>
        <button class="delete is-pulled-right" @click="$modal.hide('addPartnersModal')"></button>
      </div>
      <div class="modal-card-body">
        <div class="block">
          <div class="control has-addons">
            <div class="select">
              <select v-model="searchOptions.searchConstruction" v-if="type === 'constructor'">
                <option :value="getAllSearchOption(constructionList, 'ct_pk')"></option>
                <option v-for="construction in constructionList" :value="construction.ct_pk">{{construction.ct_name}}</option>
              </select>

              <select v-model="searchOptions.searchResourceCategory" v-if="type === 'correspondent'">
                <option :value="getAllSearchOption(resourceCategoryList, 'rc_pk')"></option>
                <option v-for="resourceCategory in resourceCategoryList" :value="resourceCategory.rc_pk">{{resourceCategory.rc_name}}</option>
              </select>
            </div>
            <input class="input" type="text" placeholder=" " v-model="searchOptions.name" @keypress.enter.stop="loadPartners">
            <a class="button is-info" @click="loadPartners"></a>
          </div>
        </div>
        <div class="partner-wrapper">
          <table class="table is-bordered" v-if="type === 'constructor'">
            <colgroup>
              <col width="7%"/>
              <col width="8%"/>
              <col width="15%"/>
              <col width="10%"/>
              <col width="10%"/>
              <col width="30%"/>
              <col width="10%"/>
            </colgroup>
            <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in listData.constructor" v-if="listData.constructor.length !== 0">
              <td>{{item.ct_name}}</td>
              <td>{{item.cr_name}}</td>
              <td>{{item.cr_contact}}</td>
              <td>
                <star-rating v-model="item.cr_communication_score" :show-rating="false" :star-size="15" :read-only="true" />
              </td>
              <td>
                <star-rating v-model="item.cs_skill_score" :show-rating="false" :star-size="15" :read-only="true" />
              </td>
              <td>{{item.cs_memo}}</td>
              <td>
                <button class="button is-primary" @click="addPartner(item)"></button>
              </td>
            </tr>
            <tr>
              <td colspan="7" v-if="listData.constructor.length === 0">  .</td>
            </tr>
            </tbody>
          </table>
          <table class="table is-bordered" v-if="type === 'correspondent'">
            <colgroup>
              <col width="10%"/>
              <col width="20%"/>
              <col width="15%"/>
              <col width="15%"/>
              <col width="30%"/>
              <col width="10%"/>
            </colgroup>
            <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in listData.correspondent" v-if="listData.correspondent.length !== 0">
              <td>{{item.rc_name}}</td>
              <td>{{item.co_name}}</td>
              <td>{{item.co_contact}}</td>
              <td>{{item.co_manager_name}}</td>
              <td>{{item.ci_brand}}</td>
              <td>
                <button class="button is-primary" @click="addPartner(item)"></button>
              </td>
            </tr>
            <tr>
              <td colspan="6" v-if="listData.correspondent.length === 0">  .</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>
  import StarRating from 'vue-star-rating'
  import Vue from 'vue'
  import Notification from 'vue-bulma-notification'
  import _ from 'underscore'

  const NotificationComponent = Vue.extend(Notification)

  const openNotification = (propsData = {
    title: '',
    message: '',
    type: '',
    direction: '',
    duration: 4500,
    container: '.notifications'
  }) => {
    return new NotificationComponent({
      el: document.createElement('div'),
      propsData
    })
  }

  const queryApi = '/api'
  const contractQueryApi = '/api/contract'
  export default {
    name: 'add-partners-modal',
    components: {
      StarRating
    },
    props: {
      title: String,
      type: String,
      id: String,
      constructionList: Array,
      resourceCategoryList: Array,
      beforeClose: Function
    },
    data () {
      return {
        listData: {
          constructor: [],
          correspondent: []
        },
        searchOptions: {
          searchConstruction: '',
          searchResourceCategory: '',
          name: ''
        }
      }
    },
    methods: {
      beforeOpen (event) {
        this.$nextTick(() => {
          this.loadPartners()
        })
      },
      loadPartners () {
        if (!this.searchOptions.searchConstruction) {
          this.searchOptions.searchConstruction = this.getAllSearchOption(this.constructionList, 'ct_pk')
        }
        if (!this.searchOptions.searchResourceCategory) {
          this.searchOptions.searchResourceCategory = this.getAllSearchOption(this.resourceCategoryList, 'rc_pk')
        }
        let url = `${queryApi}/${this.type}?pc_pk=${this.id}&ct_pk=${this.searchOptions.searchConstruction}&rc_pk=${this.searchOptions.resourceCategory}`
        switch (this.type) {
          case 'constructor':
            url = `${url}&cr_name=${this.searchOptions.name}`
            break
          case 'correspondent':
            url = `${url}&co_name=${this.searchOptions.name}`
            break
        }
        this.$http.get(url)
          .then((response) => {
            console.log(response.data.data)
            if (response.data.code !== 200) {
              return false
            }
            this.listData[this.type] = response.data.data[`${this.type}List`]
          })
      },
      addPartner (constructor) {
        console.log(`${contractQueryApi}/${this.id}/${this.type}`)
        this.$http.post(`${contractQueryApi}/${this.id}/${this.type}`, constructor)
          .then((response) => {
            console.log(response.data.data)
            if (response.data.code !== 200) {
              return false
            }
            openNotification({
              message: ' .',
              type: 'success',
              duration: 1500
            })
            this.$modal.hide('addPartnersModal')
          })
      },
      getAllSearchOption (list, key) {
        const values = _.pluck(list, key)
        return values.join(',')
      }
    }
  }
</script>
<style lang="scss" scoped>
  .add-partners-modal {
    border-radius: 1rem;
    .table {
      padding: 1rem;
    }
  }
  .v--modal-overlay {
    margin-left: -90px;
    width: calc(100% + 90px);
  }
  .partner-wrapper {
    overflow: auto;
    max-height: 350px;
  }
</style>



