<template>
  <div class="tile is-ancestor">
    <div class="tile is-parent">
      <article class="tile is-child box">
        <div class="block">
          <label class="label"></label>
          <div class="control">
            <input class="input" type="text" v-model="data.pc_name" :class="{'is-danger': $v.data.pc_name.$invalid }" />
            <p class="help is-danger" v-if="!$v.data.pc_name.required">  .</p>
          </div>
          <label class="label"></label>
          <div class="control">
            <input class="input" type="text" v-model="data.pc_phone" :class="{'is-danger': $v.data.pc_phone.$invalid }" />
            <p class="help is-danger" v-if="!$v.data.pc_phone.required">  .</p>
          </div>
          <label class="label"></label>
          <p class="control">
            <input class="input" type="text" v-model="data.pc_size" />
          </p>
          <label class="label"></label>
          <p class="control">
            <input class="input" type="text" v-model="data.pc_address_brief" />
          </p>
          <label class="label"> </label>
          <p class="control">
            <input class="input" type="text" v-model="data.pc_address_detail" />
          </p>
          <label class="label"></label>
          <p class="control">
             <datepicker placeholder=" " :config="{ dateFormat: 'Y-m-d', static: true }" v-model="data.pc_move_date"></datepicker>
          </p>
          <label class="label"></label>
          <p class="control">
            <input class="input" type="text" v-model="data.pc_budget"/>
          </p>
          <label class="label"></label>
          <p class="control">
            <textarea class="textarea" v-model="data.pc_memo"></textarea>
          </p>
          <p class="control">
            <button class="button is-link" @click="backPage"></button>
            <button class="button is-primary" @click="registerContract"></button>
          </p>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import Notification from 'vue-bulma-notification'
  import router from '../../router'
  import { required } from 'vuelidate/lib/validators'
  import Datepicker from 'vue-bulma-datepicker'

  const NotificationComponent = Vue.extend(Notification)

  const queryApi = '/api/contract'

  const openNotification = (propsData = {
    title: '',
    message: '',
    type: '',
    direction: '',
    duration: 4500,
    container: '.notifications'
  }) => {
    return new NotificationComponent({
      el: document.createElement('div'),
      propsData
    })
  }

  export default {
    name: 'register',
    components: {
      Datepicker
    },
    data () {
      return {
        data: {
          pc_name: '',
          pc_phone: ''
        }
      }
    },
    validations: {
      data: {
        pc_name: {
          required
        },
        pc_phone: {
          required
        }
      }
    },
    methods: {
      registerContract () {
        this.$http.post(`${queryApi}`, this.data)
          .then((response) => {
            if (response.data.code !== 200) {
              openNotification({
                message: '  .   .',
                type: 'danger',
                duration: 1500
              })
              return false
            }
            openNotification({
              message: '.',
              type: 'success',
              duration: 1500
            })
            router.back()
          })
          .catch((error) => {
            console.log(error)
          })
      },
      backPage () {
        router.back()
      }
    }
  }
</script>

<style scoped>

</style>



