<template>
  <modal name="addSiteImageModal" height="auto" width="60%" @before-close="beforeClose">
    <div class="add-site-image-modal">
      <div class="modal-card-head">
        <h1 class="modal-card-title"> </h1>
        <button class="delete is-pulled-right" @click="$modal.hide('addSiteImageModal')"></button>
      </div>
      <div class="modal-card-body">
        <div class="partner-wrapper">
          <div class="control">
            <span class="input-group" v-for="(siteImage, index) in siteImageList">
              <img :src=siteImage.si_url v-on:click="callFileUpload('file_upload_' + index)" class="site-image"/>
              <button class="button delete-btn" @click="deletedImage(index)"></button>
              <input type="file" :name='"portfolio_after[" + index + "]"' placeholder="After" style="display:none;" :ref='"file_upload_" + index' v-on:change="onFileChanged($event, index)" />
              <input type="text" class="input" v-model="siteImage.si_description" placeholder=" " />
            </span>
            <span class="input-group">
              <button v-on:click="callFileUpload('file_upload_new')">  </button>
              <input type="file" name="new_file" placeholder="new File" style="display:none;" :ref='"file_upload_new"' v-on:change="onFileChanged($event, 'new')" />
            </span>
          </div>
        </div>
        <button class="button is-info" @click="uploadSiteImages"></button>
      </div>
    </div>
  </modal>
</template>

<script>
  import FormData from 'form-data'
  import Notification from 'vue-bulma-notification'
  import Vue from 'vue'

  const NotificationComponent = Vue.extend(Notification)

  const openNotification = (propsData = {
    title: '',
    message: '',
    type: '',
    direction: '',
    duration: 4500,
    container: '.notifications'
  }) => {
    return new NotificationComponent({
      el: document.createElement('div'),
      propsData
    })
  }

  const fileUploadApi = '/api/file/upload'
  const queryApi = '/api/contract'

  export default {
    name: 'add-site-image-modal',
    props: {
      id: String,
      beforeClose: Function
    },
    data () {
      return {
        siteImageList: [],
        newDescription: ''
      }
    },
    methods: {
      callFileUpload (index) {
        const currentTarget = this.$refs[index]
        if (currentTarget.length > 0) {
          currentTarget[0].click()
        } else {
          currentTarget.click()
        }
      },
      onFileChanged (event, index) {
        const formData = new FormData()
        formData.append('file_upload_path', 'portfolio')
        formData.append('filedata', event.target.files[0])

        this.$http.post(fileUploadApi, formData)
          .then((response) => {
            // if ()
            var responseData = response.data

            if (responseData.code === 200) {
              const imageData = responseData.data
              if (index === 'new') {
                index = this.siteImageList.length
              }
              this.siteImageList[index] = {
                si_description: '',
                si_url: imageData.value
              }
              this.$forceUpdate()
            }
          })
          .catch((error) => {
            console.log(error)
          })
      },
      deletedImage (index) {
        this.siteImageList.splice(index, 1)
      },
      uploadSiteImages () {
        this.$http.post(`${queryApi}/${this.id}/image`, {
          si_image_list: this.siteImageList
        })
        .then((response) => {
          if (response.data.code !== 200) {
            openNotification({
              message: '   .',
              type: 'danger',
              duration: 1500
            })
            return false
          }
          openNotification({
            message: '.',
            type: 'success',
            duration: 1500
          })

          this.siteImageList = []
          this.newDescription = ''

          this.$modal.hide('addSiteImageModal')
        })
        .catch((e) => {
          console.log(e)
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .add-site-image-modal {
    border-radius: 1rem;
    .table {
      padding: 1rem;
    }
  }
  .v--modal-overlay {
    margin-left: -90px;
    width: calc(100% + 90px);
  }
  .partner-wrapper {
    overflow: auto;
    max-height: 350px;
  }
  .site-image {
    width:250px;
  }
</style>



